.product_index_container{
    background: #fafafa;
    .products_filter_container_parent{
        margin-top: 25px;
        padding-bottom: 100px;
        .products_filter_container{
            display: inline-flex;
            justify-content: center;
            width: 100%;
            .sub_catagory_filter_parent{
                padding-left: 5px;
                width: 25%;
                .sub_catagory_filter{
                    background: #fff;
                    box-shadow: 0px 0px 1px #8c8686;
                    .single_catagory{
                        cursor: pointer;
                        padding: 10px;
                        border-bottom: 1px solid #e4e4e4;
                        width: 100%;
                        align-items: baseline;

                        .content{
                            text-transform: uppercase;
                            color: #282828;
                            font-size: 12px;
                            font-weight: 100;
                            &:hover{
                                color: #fc7070;
                                text-decoration: none;
                            }
                        }
                    }
                    .single_catagory1{
                        cursor: pointer;
                        width: 100%;
                        align-items: baseline;
                        padding-left: 10px;
                        a{
                            &:hover{
                                color: #fc7070;
                                text-decoration: none;
                            }
                        }
                    }
                }
            }
            .products_container{
                width: 75%;
                .search_container{
                    position: relative;
                    padding: 0 22px 20px;
                    input{
                        height: 35.23px;
                        padding: 2px 17px 2px 35px;
                        border-radius: 6px;
                        font-size: 16px;
                        line-height: 22px;
                        width: 100%;
                        border: none;
                        box-shadow: 0 0px 20px #ddd;
                    }
                    .seacr_icon{
                        left: 31px;
                        position: absolute;
                        top: 9px;
                        color: #bbb;
                    }
                }
                .products_row{
                    margin: 0;
                    padding: 0 10px;
                    justify-content: space-between;
                    .product_container{
                        background: #fff;
                        margin-bottom: 10px;
                        height: 420px;
                        a{
                            color: #000;
                            &:hover{
                                text-decoration: none;
                            }
                        }
                        &:hover{
                            box-shadow: 0px 1px 2px #ddd;
                        }
                        .image_parent{
                            height: 300px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            img{
                                max-height: 300px;
                                max-width: 100%;
                            }
                        }
                        .content{
                            color: #282828;
                            font-size: 12px;
                            text-transform: uppercase;
                            line-height: 20px;
                            .items_name{
                                padding: 0 10px 0;
                                text-align: center;
                                display: block; /* or inline-block */
                                text-overflow: ellipsis;
                                word-wrap: break-word;
                                overflow: hidden;
                                max-height: 3.6em;
                                line-height: 1.8em;
                                height: 3.6em;
                            }
                            .price{
                                padding: 0 10px 0;
                                display: inline-flex;
                                justify-content: center;
                                width: 100%;
                                .current_price{
                                    margin-right: 10px;
                                    font-weight: 600;
                                }
                                .prev_price{
                                    margin-left: 10px;
                                    position: relative;
                                    .cross{
                                        width: 40px;
                                        position: absolute;
                                        top: 1px;
                                        left: 6px;
                                    }
                                }
                            }
                            .learn_more{
                                text-align: center;
                                cursor: pointer;
                                padding: 10px;
                                font-weight: 600;
                                margin-top: 5px;
                                &:hover{
                                    box-shadow: 0 0 5px #ddd;
                                }
                            }
                        }
                    }
                }
              
            }
        }
    }
}
@media all and(max-width: 767px){
    .product_index_container .products_filter_container_parent .products_filter_container .sub_catagory_filter_parent{
        display: none;
    }
    .product_index_container .products_filter_container_parent .products_filter_container .products_container{
        width: 100%;
    }
    .product_index_container .products_filter_container_parent .products_filter_container .products_container .products_row .product_container{
        height: unset;
        .image_parent{
            img{
                height: 200px;
            }
        }
    }
}